.main-color {
  color: #212772;
}
.split-line-color {
  color: #000000;
}
.layout-menu-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.layout-menu-item {
  line-height: 32px;
  font-family: 'Sofia Pro Bold', 'Sofia Pro', Serif;
  font-style: italic;
  color: #212772;
  cursor: pointer;
}
.layout-slider {
  margin-top: 6px;
  height: 16px;
}
.layout-slider-line {
  width: 1600px;
  height: 1px;
  background-color: #000000;
}
.layout-slider-dot {
  transition: 0.6s ease-in-out;
  float: left;
  display: inline-block;
  height: 16px;
  width: 16px;
  background-color: #212772;
  border-radius: 50%;
}
