.main-color {
  color: #212772;
}
.split-line-color {
  color: #000000;
}
.banner-container {
  margin-top: 117px;
  margin-bottom: 100px;
}
.title {
  display: flex;
}
.picture {
  width: 741px;
  height: 373px;
}
.name {
  display: inline-block;
  margin-left: 60px;
}
.name .welcome {
  display: table-cell;
  vertical-align: bottom;
  height: 52px;
  line-height: 35px;
  font-size: 26.4px;
  font-family: "Sofia Pro Light";
  color: #000000;
}
.name .actual-name {
  margin-top: 71px;
  transform: translateX(-7.5px);
}
.name .actual-name span {
  font-size: 108px;
  line-height: 108px;
  height: 108px;
  color: #212772;
}
.name .actual-name .chinese-name {
  font-family: "Sofia Pro Black";
}
.name .actual-name .english-name {
  font-family: "Sofia Pro Ultra Light";
}
.name .who-am-i {
  font-size: 26.4px;
  line-height: 35px;
  font-family: "Sofia Pro Light";
}
.metaphor {
  margin-top: 27px;
  font-family: 'Sofia Pro Bold', 'Sofia Pro', Serif;
  font-style: italic;
  color: #212772;
  font-size: 23px;
  line-height: 23px;
}
